import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types';

/*
 * Class which creates and renders a google map. Using the Google Map React package found a
 *  https://www.npmjs.com/package/google-maps-react
 *  https://github.com/fullstackreact/google-maps-react#automatically-lazy-loading-google-api
 * Most of the code here is copied from
 *  https://www.fullstackreact.com/articles/how-to-write-a-google-maps-react-component/
 *  https://medium.com/front-end-hacking/using-the-google-maps-javascript-api-in-a-react-project-b3ed734375c6
 */
export class Map extends React.Component {
  constructor(props) {
    super(props);

    const {lat, lng} = this.props.initialCenter;
    this.state = {
      currentLocation: {
        lat: lat,
        lng: lng
      }
    }
    
    this.recenterMap = this.recenterMap.bind(this);
    this.renderChildren = this.renderChildren.bind(this);
  }
  
  componentDidMount() {
    //Get the users location with the help of HTML Gelocation API
    if (this.props.centerAroundCurrentLocation) {
        if (navigator && navigator.geolocation) {
            navigator.geolocation.getCurrentPosition((pos) => {
                const coords = pos.coords;
                this.setState({
                    currentLocation: {
                        lat: coords.latitude,
                        lng: coords.longitude
                    }
                })
            })
        }
    }
    this.loadMap();
  }

  componentDidUpdate(prevProps, prevState) {
    //If google prop has changed => loadMap
    if (prevProps.google !== this.props.google) {
      this.loadMap();
    }
    //If loadmap prop has changed => loadMap
    else if(prevProps.loadmap !== this.props.loadmap) {
      this.loadMap();
    }
    //If user lucation has changed => recenter map
    if (prevState.currentLocation !== this.state.currentLocation) {
      this.recenterMap();
    }
  }
  
  //Method to recenter map around current user location.
  //Requires that map is available.
  recenterMap() {
    const map = this.map;
    const curr = this.state.currentLocation;
    const google = this.props.google;

    if (map) {//If map is available => recenter map
        const maps = google.maps;
        let center = new maps.LatLng(curr.lat, curr.lng)
        map.panTo(center)
    }
  }

  //Method to load basic google map.
  loadMap() {
    //load map only when google is available and props loadmap is set to true
    if (this.props && this.props.google && this.props.loadmap === true) {
      const {google} = this.props;
      const maps = google.maps;
      
      const mapRef = this.refs.map;
      const node = ReactDOM.findDOMNode(mapRef);
      
      let {initialCenter, zoom} = this.props;
      const {lat, lng} = this.state.currentLocation;
      const center = new maps.LatLng(lat, lng);
      const mapConfig = Object.assign({}, {
        center: center,
        zoom: zoom
      })
      this.map = new maps.Map(node, mapConfig);
    }
  }
  
  //Render children of map. Children are passed in as 'arguments' by the App class.
  //In this case the children are two Marker classes.
  //Only render the children if they are available (i.e. passed in as arguments to the Map class)
  //or the prop loadmap is set to true (we cannot add markers without map).
  renderChildren() {
    const {children} = this.props;

    if (!children) return;
    
    if(this.props.loadmap === false) return;
    
    return React.Children.map(children, c => {
      return React.cloneElement(c, {
        map: this.map,
        google: this.props.google,
        mapCenter: this.state.currentLocation
      });
    })
  }

  render() {
    const style = {
      width: '100%',
      height: '50vh'
    }
    return (
      <div ref='map' style={style}>
        Press button to find nearby Systembolag
        {this.renderChildren()}
      </div>
    )
  }
}

Map.propTypes = {
}

Map.defaultProps = {
  zoom: 16,
  initialCenter: {// Home by default
    lat: 56.0496456,
    lng: 12.6978903
  },
  centerAroundCurrentLocation: true
}

export default Map;


