import React from 'react';
 
class SearchResultItems extends React.Component {
  constructor(props, context) {
    super(props, context);
 
    this.addStores = this.addStores.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }
 
  addStores(item) {
    var listItem = <li onClick={(e)=>this.handleClick(item.key, e)} key={item.key}>
                    {item.street}, {item.dist}m
                  </li>
    return listItem
  }
  
  handleClick(key) {
    this.props.handleClick(key);
  }
 
  render() {
    /*
     * Take the list of searchResultItems items (passed in as entries)
     * and turn them into JSX/HTML-ish elements
     */
    var searchResultEntries = this.props.entries;
    var listItems = searchResultEntries.map(this.addStores);
    return (
      <ul>
          {listItems}
      </ul>
    );
  }
};
 
export default SearchResultItems;


