import React, { Component } from 'react';
import './App.css';
import SearchResultItems from './SearchResultItems';
import { Map } from './Map';
import {InfoWindow, GoogleApiWrapper} from 'google-maps-react';
import Marker from './Marker';

class App extends Component {
  constructor(props, context) {//Class constructor
    super(props, context);
    
    //Class state variables which will trigger rerendering when changed
    this.state = {
      stores: [],//Holds search result stores, i.e. stores
      userLocation: {
        lat: 0,
        lng: 0,
      },
      markerLocation: {
        lat: 0,
        lng: 0,
      },
      userCity: "",
      storeData: 0,
      storeStreet: "",
      loadMap: false,
    };
    
    /* 
     * Ensure all references to this inside below function will correctly
     * reference objects inside this class.
     */
    this.showStoresInList = this.showStoresInList.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.setUserPosition = this.setUserPosition.bind(this);
    this.setUserCity = this.setUserCity.bind(this);
    this.convert_rt90 = this.convert_rt90.bind(this);
  }

  componentDidMount() {
    /* 
     * Try using HTML5 geolocation API to get user location, i.e.
     * latitude and longitude.
     * When we have the user location (lat and lng), use the Google Maps API
     * to request the address corresponding to a given latitude/longitude.
     * 
     * For more information how to do this see;
     *  Geolocation API
     *    http://html5doctor.com/finding-your-position-with-geolocation/
     *    https://www.w3.org/TR/geolocation-API/
     *  Google Maps API
     *    https://developers.google.com/maps/documentation/javascript/geolocation?hl=en
     *    https://developers.google.com/maps/documentation/geocoding/start?csw=1#ReverseGeocoding
     */
    var setUserPositionFunction = this.setUserPosition;//bind this.setuserpos to local variable
    var setCityFunction = this.setUserCity;//bind this.setUserCity to local variable
    var getGoogleMapsUrlString = function(lat, lng) {
      var googleMapsUrl = "https://maps.googleapis.com/maps/api/geocode/json?latlng=";
      var latString = encodeURIComponent(lat);
      var lngString = encodeURIComponent(lng);
      var apiKey = "&key=AIzaSyDXV7jwjZ6mBiGRe2DwUH7qaw-icfY5Hg0"
      return googleMapsUrl + latString + "," + lngString + apiKey;
    };
    var getUserCityFromGoogleMapsAPI = function(url) {
      fetch(url)
        .then(res => res.json())
        .then(
          (result) => {
            //The 'path' to the city/town is hardcoded here.
            //TBD: Have some kind of json lookup to control that I actually get a town/city
            setCityFunction(result.results[0].address_components[3].long_name);
          },
          (error) => {
            console.log(error);
            setCityFunction("Helsingborg (Default)");
          }
        )
    };
    //If HTML5 Geolocation API is available, get the user location
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(function(position) {
        var pos = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
        setUserPositionFunction(pos);//since I am inside anonymous function I cant use 'this' keyword
        getUserCityFromGoogleMapsAPI(getGoogleMapsUrlString(pos.lat, pos.lng));
      }, function() {
        // Something went wrong. Set some default position, right now it is Helsingborg
        var pos = {
          lat: 56.046920099999994,
          lng: 12.7058757
        };
        setUserPositionFunction(pos);//since I am inside anonymous function I cant use 'this' keyword
        getUserCityFromGoogleMapsAPI(getGoogleMapsUrlString(pos.lat, pos.lng));
      });
    }
    else {
      // Browser doesn't support Geolocation. Set some default position, right now it is Helsingborg
      var pos = {
        lat: 56.046920099999994,
        lng: 12.7058757
      };
      setUserPositionFunction(pos);//since I am inside anonymous function I cant use 'this' keyword
      getUserCityFromGoogleMapsAPI(getGoogleMapsUrlString(pos.lat, pos.lng));
    }
    
    /* 
     * Download the systembolagets 'storeData'.
     * When I started this project I read on https://www.systembolaget.se/api
     * that the systembolaget had an API for inventory and stores.
     * Later when I has started the project and invested time and effort in it,
     * I found out that their server does not allow cross-origin-access, hence
     * I cannot make a fetch or ajax request from outside systembolaget.se.
     * To work around this I tried to request with mode 'no-cors' and when
     * when doing that I get a response but it is a failure response, i.e.
     * the server does not allow me to read the data.
     * As last resort I have manually downloaded the xml-file provided by the
     * systembolagets api (https://www.systembolaget.se/api/assortment/stores/xml)
     * and dowloading it from the local server.
     */
    var loadStoreData = function(url) {return fetch(url)};
    var loadStoreDataNoCors = function(url) {return fetch(url, {'mode': 'no-cors'})};
    var extractXmlButiksData = function(response) {
      return response.text()
      .then(str => (new window.DOMParser()).parseFromString(str, "text/xml"))};
    loadStoreData('https://www.systembolaget.se/api/assortment/stores/xml').then(
      (response) => {//If we get a response, extract the data
        extractXmlButiksData(response).then(data => {
          this.setState({storeData: data});
        });
      },
      (error) => {//If we do not get a response, try without mode 'No Access Origin Restriction'
        loadStoreDataNoCors('https://www.systembolaget.se/api/assortment/stores/xml').then(
          (response) => {
            if(response.status === 200) {
              extractXmlButiksData(response).then(data => {
                this.setState({storeData: data});
              });
            }
            else {//If all fails, download the local xml
              loadStoreData('http://henrik.eideberg.se/projects/hittasystemet/data/xml.xml').then((response) => {
                extractXmlButiksData(response).then(data => {
                  this.setState({storeData: data});
                });
              });
            }
          },
          (error) => {//If all fails, download the local xml
            loadStoreData('http://henrik.eideberg.se/projects/hittasystemet/data/xml.xml').then((response) => {
              extractXmlButiksData(response).then(data => {
                this.setState({storeData: data});
              });
            });
          }
        );
      }
    );
  }

  //Method to set the state user location/position
  setUserPosition(pos) {
    this.setState({
      userLocation: {
        lat: pos.lat,
        lng: pos.lng,
      },
    });
  }
  
  //Method to set the state user city
  setUserCity(city) {
    this.setState({
      userCity: city
    });
  }
  
  /*
   * Define our showStoresInList event handler.
   * This event creates a store item and adds it to the state stores array.
   */
  showStoresInList(event) {
    //Go through storeData and save all stores which are located in the userCity
    var storeArray = [];
    var x = this.state.storeData.documentElement.childNodes;
    var nodeLength;
    var lat_lng_array = new Array(2);
    var posA;
    var posB;
    var distance;
    for(var i=0; i<x.length; i++) {
      if(x[i].nodeName === "ButikOmbud") {
        if(x[i].childNodes[6].nodeName === "Address4") {
          if(x[i].childNodes[6].innerHTML.toUpperCase() === this.state.userCity.toUpperCase()) {
            //convert the rt90 coordinates to latitude and longitude
            //assume rt90x is at position length-2 and rt90y is at position length-1
            nodeLength = x[i].childNodes.length;
            lat_lng_array = this.convert_rt90(x[i].childNodes[nodeLength-2].innerHTML, x[i].childNodes[nodeLength-1].innerHTML);

            posA = new this.props.google.maps.LatLng(lat_lng_array[0], lat_lng_array[1]);
            posB = new this.props.google.maps.LatLng(this.state.userLocation.lat, this.state.userLocation.lng);
            distance = this.props.google.maps.geometry.spherical.computeDistanceBetween(posA,posB);
            //Save all relevant store information in the store array
            var storeArrayItem = {
              key: "hittasystemet" + String(i) + Date.now(), //Create unique key
              street: x[i].childNodes[3].innerHTML,
              lat_lng: lat_lng_array,
              dist: ~~distance//convert to int by inverting all the bits two times (converts to int in the process)
            }
            storeArray.unshift(storeArrayItem);//Add store(s) to beginning of array
          }
        }
      }
    }
    
    //If no stores found in user city widen the search
    if(storeArray && storeArray.length === 0) {
      var allStoresArray = [];
      for(var j=0; j<x.length; j++) {
        if(x[j].nodeName === "ButikOmbud") {
          if(x[j].childNodes[6].nodeName === "Address4") {
            //convert the rt90 coordinates to latitude and longitude
            //assume rt90x is at position length-2 and rt90y is at position length-1
            nodeLength = x[j].childNodes.length;
            lat_lng_array = this.convert_rt90(x[j].childNodes[nodeLength-2].innerHTML, x[i].childNodes[nodeLength-1].innerHTML);

            posA = new this.props.google.maps.LatLng(lat_lng_array[0], lat_lng_array[1]);
            posB = new this.props.google.maps.LatLng(this.state.userLocation.lat, this.state.userLocation.lng);
            distance = this.props.google.maps.geometry.spherical.computeDistanceBetween(posA,posB);
            //Save all store information in the store array
            var allStoresArrayItem = {
              key: "hittasystemet" + String(j) + Date.now(), //Create unique key
              street: x[j].childNodes[3].innerHTML,
              lat_lng: lat_lng_array,
              dist: ~~distance//convert to int by inverting all the bits two times (converts to int in the process)
            }
            allStoresArray.unshift(allStoresArrayItem);//Add store(s) to beginning of array
          }
        }
      }
      //sort the allStoresArray in ascending order and then put top three in storeArray
      allStoresArray.sort(function(a,b){
        return parseFloat(a.dist) - parseFloat(b.dist)
      });
      for(var k=0; k<3; k++) {
        storeArray.unshift(allStoresArray[j]);
      }
    }
    
    //Sort the storeArray in ascending order
    storeArray.sort(function(a,b){
      return parseFloat(a.dist) - parseFloat(b.dist)
    });

    //Update stores and loadMap state vars. When setting loadMap to true, the google map will show.
    this.setState({
      stores: storeArray,
      loadMap: true
    });
    event.preventDefault(); //Override this event's default behavior.
  }
  
  /* 
   * Define our handleClick event handler.
   * We are passing the key from our clicked item all the way to here. Once here we get the
   * position of the clicked item and update our state variable markerLocation. This will
   * trigger an update of the map and add a marker to the map (showing position of the store).
   */
  handleClick(clickedKey) {
    var clickedItem = this.state.stores.filter(function (store) {
      return (store.key === clickedKey);
    });
    this.setState({
      markerLocation: {
        lat: clickedItem[0].lat_lng[0],
        lng: clickedItem[0].lat_lng[1],
      },
      storeStreet: clickedItem[0].street
    });
  }
  
  /*
   * Convert rt90 coordinates to latitude and longitude using rt90_2.5_gon_v
   * Copied from http://www.trf.se/rt90/
   */
  convert_rt90(x, y) {
    var math_sinh = function(value) {return 0.5 * (Math.exp(value) - Math.exp(-value))};
    var math_cosh = function(value) {return 0.5 * (Math.exp(value) + Math.exp(-value))};
    //var math_atanh = function(value) {return 0.5 * Math.log((1.0 + value) / (1.0 - value))};
    
    //init variables for rt90_2.5_gon_v (default)
    var axis = 6378137.0; // GRS 80.
    var flattening = 1.0 / 298.257222101; // GRS 80.
    //var lat_of_origin = 0.0;
    var central_meridian = 15.0 + 48.0/60.0 + 22.624306/3600.0;
    var scale = 1.00000561024;
    var false_northing = -667.711;
    var false_easting = 1500064.274;
    var lat_lon = new Array(2);

    // Prepare ellipsoid-based stuff.
    var e2 = flattening * (2.0 - flattening);
    var n = flattening / (2.0 - flattening);
    var a_roof = axis / (1.0 + n) * (1.0 + n*n/4.0 + n*n*n*n/64.0);
    var delta1 = n/2.0 - 2.0*n*n/3.0 + 37.0*n*n*n/96.0 - n*n*n*n/360.0;
    var delta2 = n*n/48.0 + n*n*n/15.0 - 437.0*n*n*n*n/1440.0;
    var delta3 = 17.0*n*n*n/480.0 - 37*n*n*n*n/840.0;
    var delta4 = 4397.0*n*n*n*n/161280.0;
    
    var Astar = e2 + e2*e2 + e2*e2*e2 + e2*e2*e2*e2;
    var Bstar = -(7.0*e2*e2 + 17.0*e2*e2*e2 + 30.0*e2*e2*e2*e2) / 6.0;
    var Cstar = (224.0*e2*e2*e2 + 889.0*e2*e2*e2*e2) / 120.0;
    var Dstar = -(4279.0*e2*e2*e2*e2) / 1260.0;

    // Convert.
    var deg_to_rad = Math.PI / 180;
    var lambda_zero = central_meridian * deg_to_rad;
    var xi = (x - false_northing) / (scale * a_roof);		
    var eta = (y - false_easting) / (scale * a_roof);
    var xi_prim = xi - 
            delta1*Math.sin(2.0*xi) * math_cosh(2.0*eta) - 
            delta2*Math.sin(4.0*xi) * math_cosh(4.0*eta) - 
            delta3*Math.sin(6.0*xi) * math_cosh(6.0*eta) - 
            delta4*Math.sin(8.0*xi) * math_cosh(8.0*eta);
    var eta_prim = eta - 
            delta1*Math.cos(2.0*xi) * math_sinh(2.0*eta) - 
            delta2*Math.cos(4.0*xi) * math_sinh(4.0*eta) - 
            delta3*Math.cos(6.0*xi) * math_sinh(6.0*eta) - 
            delta4*Math.cos(8.0*xi) * math_sinh(8.0*eta);
    var phi_star = Math.asin(Math.sin(xi_prim) / math_cosh(eta_prim));
    var delta_lambda = Math.atan(math_sinh(eta_prim) / Math.cos(xi_prim));
    var lon_radian = lambda_zero + delta_lambda;
    var lat_radian = phi_star + Math.sin(phi_star) * Math.cos(phi_star) * 
            (Astar + 
             Bstar*Math.pow(Math.sin(phi_star), 2) + 
             Cstar*Math.pow(Math.sin(phi_star), 4) + 
             Dstar*Math.pow(Math.sin(phi_star), 6));
    lat_lon[0] = lat_radian * 180.0 / Math.PI;
    lat_lon[1] = lon_radian * 180.0 / Math.PI;
    return lat_lon;
  }
  
  render() {
    //Save the markerLocations in a format that is handy for the Marker class.
    //If no position is given to the Marker - then the users location will be used.
    const markerPos = {lat: this.state.markerLocation.lat, lng: this.state.markerLocation.lng}
    return (
      <div className="App">
        <div className="form">
          {
            /* 
            * Render the form, se https://reactjs.org/docs/forms.html for more
            * information on forms in React.
            * We listen for the submit event on the form itself,
            * and we call the showStoresInList method when that event is overheard
            */
          }
          <form onSubmit={this.showStoresInList}>
            <button type="submit" className="submit">Hitta Systemet</button>
          </form>
        </div>
        <div className="list">
          <SearchResultItems entries={this.state.stores}
                             handleClick={this.handleClick}/>
        </div>
        <div className="mapWrapper">
          <Map google={this.props.google}
               loadmap={this.state.loadMap}>
            <Marker />
            <Marker position={markerPos} />
            <InfoWindow
              position={markerPos}
              visible={true}>
                <div>
                  <h1>{this.state.storeStreet}</h1>
                </div>
            </InfoWindow>
          </Map>
        </div>
      </div>
    );
  }
}

//export the container within the GoogleApiWrapper to make our App communicate with Google Maps API
export default GoogleApiWrapper({
  apiKey: 'AIzaSyDXV7jwjZ6mBiGRe2DwUH7qaw-icfY5Hg0',
  libraries: ['geometry'] //needed to calculate the distance between two google map locations
})(App)



